import React from "react";
import { Navigate } from "react-router-dom";

function ProtectedRoute({ isAuthenticated, children }) {
  //   
  if (isAuthenticated === null) {
    return null; //      .
  }

  if (!isAuthenticated) {
    return <Navigate to="/Signin" replace />;
  }

  return children;
}

export default ProtectedRoute;
