import React, { useEffect, useState, useRef } from "react";
import styled from "styled-components";
import LikeButton from "./LikeButton";

const MoviesGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  margin-bottom: 20px;
`;

const MovieThumbnailWrapper = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  align-items: flex-end;
  transition: transform 0.3s ease; /*     */
  transform: scale(1);

  &:hover {
    transform: scale(1.05); /*    */
  }
`;

const MovieThumbnail = styled.img`
  width: 100%;
  border-radius: 5px;
  transition: transform 0.3s ease; /*    */

  /*   hover  */
  ${MovieThumbnailWrapper}:hover & {
    transform: scale(1.05);
  }
`;

const LikeButtonWrapper = styled.div`
  position: absolute;
  top: 10px;
  right: 10px;
`;

const LoadingMessage = styled.div`
  margin: 20px 0;
  font-size: 1.2rem;
  text-align: center;
  color: #007bff;
`;

const ScrollToTopButton = styled.button`
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  display: ${(props) => (props.visible ? "block" : "none")};
`;
const MovieTitleOverlay = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  min-height: 40px; /*       */
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  text-align: center;
  padding: 5px;
  box-sizing: border-box;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre-wrap;
  transition: transform 0.3s ease;

  /*   hover  */
  ${MovieThumbnailWrapper}:hover & {
    transform: scale(1.05);
  }
`;

function InfinityScrollView({ fetchMovies, movies, loading }) {
  const [showScrollToTop, setShowScrollToTop] = useState(false);
  const pressTimer = useRef(null);

  const handleScroll = () => {
    const { scrollTop, scrollHeight, clientHeight } = document.documentElement;

    //    fetchMovies 
    if (!loading && scrollHeight - scrollTop <= clientHeight * 1.5) {
      fetchMovies();
    }

    //    "  "  
    setShowScrollToTop(scrollTop > 200);
  };

  //      
  useEffect(() => {
    const checkContentHeight = () => {
      const { scrollHeight, clientHeight } = document.documentElement;
      //      
      if (!loading && scrollHeight <= clientHeight) {
        fetchMovies();
      }
    };

    checkContentHeight(); //  
  }, [movies, loading, fetchMovies]);

  //     
  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [loading]);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };
  const handleLongPress = (movie) => {
    const naverSearchUrl = `https://search.naver.com/search.naver?query= ${encodeURIComponent(
      movie.title
    )}`;
    window.open(naverSearchUrl, "_blank");
  };

  const handlePressStart = (movie) => {
    //    
    pressTimer.current = setTimeout(() => handleLongPress(movie), 500); // 1(1000ms)     
  };

  const handlePressEnd = () => {
    //    
    if (pressTimer.current) {
      clearTimeout(pressTimer.current);
    }
  };

  return (
    <>
      <MoviesGrid>
        {movies.map((movie) => (
          <MovieThumbnailWrapper key={movie.id}>
            <MovieThumbnail
              src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
              alt={movie.title}
              onMouseDown={() => handlePressStart(movie)} //     
              onMouseUp={handlePressEnd} //     
              onMouseLeave={handlePressEnd} //    
              onTouchStart={() => handlePressStart(movie)} //      
              onTouchEnd={handlePressEnd} //      
            />
            <LikeButtonWrapper>
              <LikeButton movie={movie} />
            </LikeButtonWrapper>
            <MovieTitleOverlay>{movie.title}</MovieTitleOverlay>{" "}
            {/*    */}
          </MovieThumbnailWrapper>
        ))}
      </MoviesGrid>
      {loading && <LoadingMessage>Loading...</LoadingMessage>}
      <ScrollToTopButton visible={showScrollToTop} onClick={scrollToTop}>
         
      </ScrollToTopButton>
    </>
  );
}

export default InfinityScrollView;
